import * as React from 'react'
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Typography } from '@mui/material';
import moment from 'moment';
moment.locale('ru');


function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
}



const rows = [
    createData(1, '8:00 - 8:45', '', '', '', '', ' 1 '),
    createData(2, '8:55 - 9:40', '', '', '', '', ' 1 '),
    createData(3, '9:55 - 10:40', '- ', ' 1 ', '', '', ' 2 '),
    createData(4, '11:00 - 11:45', '- ', ' 1 ', '', '', ' 2 '),
    createData(5, '12:05 - 12:50', ' 53 ', ' 30', '- ', ' 53 ', ''),
    createData(6, '13:00 - 13:45', ' 53 ', ' 30', '- ', ' 53 ', ''),
    createData(7, '13:55 - 14:40', ' 2 ', '', ' 53 ', ' 30', ''),
    createData(8, '14:50 - 15:35', ' 2 ', '', ' 53 ', ' 30', ''),
    createData(9, '15:45 - 16:30', '', '', '', '', ''),
    createData(10, '16:40 - 17:25', '', '', '', '', ''),
];

export const Schedule = (props) => {



    return (
        <>
            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell sx={{ width: 10, padding: 0 }}></TableCell>
                            <TableCell sx={{ width: 70, padding: 0 }}></TableCell>
                            <TableCell align="left"> <br /><Typography variant="caption"></Typography> </TableCell>
                            <TableCell align="left"> <br /><Typography variant="caption"></Typography> </TableCell>
                            <TableCell align="left"> <br /><Typography variant="caption"></Typography> </TableCell>
                            <TableCell align="left"> <br /><Typography variant="caption"></Typography> </TableCell>
                            <TableCell align="left"> <br /><Typography variant="caption"></Typography> </TableCell>
                            <TableCell align="left"> <br /> <Typography variant="caption"></Typography></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow
                                key={row.name}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell sx={{ width: 20, padding: 0 }} component="th" scope="row">
                                    {row.name}
                                </TableCell>
                                <TableCell sx={{ width: 70, padding: 0 }}>{row.calories}</TableCell>
                                <TableCell align="center">{row.fat}</TableCell>
                                <TableCell align="center">{row.carbs}</TableCell>
                                <TableCell align="center">{row.carbs}</TableCell>
                                <TableCell align="center">{row.carbs}</TableCell>
                                <TableCell align="center">{row.carbs}</TableCell>
                                <TableCell align="center">{row.protein}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>

        </>
    )
}

